@use '../helpers/' as *;

.clients {
  &__body {
    width: 88%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    align-self: start;
    row-gap: fluid(40, 20);

    @include laptop {
      width: 100%;
    }

    @include mobile {
      row-gap: fluid(60, 40);
    }
  }

  &__content {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: rem(100);
    margin-bottom: rem(60);

    @include laptop {
      // grid-template-columns: 1fr 1fr;
      column-gap: fluid(60, 40);
      margin-bottom: rem(20);
    }

    @include mobile {
      grid-template-columns: 1fr;
      grid-template-columns: 100%;
      row-gap: fluid(40, 20);
      margin-bottom: 0;
    }
  }

  &__text-slider {
    &-wrapper {
      width: 100%;
      display: flex;
      position: relative;
      overflow: hidden;
    }

    &-item {
      display: flex;
      flex-direction: column;
      row-gap: fluid(40, 20);
      transition: opacity 1s ease-in-out;
    }
  }

  &__slider {
    height: var(--slider-height);
    position: relative;
    overflow: hidden;
    
    @include laptop {
      height: var(--slider-height-mobile);
    }

  // Затемнение по краям
    &::after {
      content: "";
      position: absolute;
      inset: 0;
      background: radial-gradient(
        circle at center,
        rgba(15, 17, 35, 0) 40%,
        rgba(15, 17, 35, 0.6) 70%,
        rgba(15, 17, 35, 1) 100%
      );
      pointer-events: none;
      z-index: 2;
    }
  }

  &__video {
    width: 100%;
    height: 100%;
    position: absolute;
    inset: 0;
    object-fit: cover; // растягивает видео по контейнеру, не искажая пропорции
    object-position: center center;
    background-color: var(--background-basic);
    mix-blend-mode: lighten;
    opacity: 0.9;
    // Важно для iOS Safari
    display: block;
    pointer-events: none;
    transform: translateZ(0);
    backface-visibility: hidden;
    z-index: 1;

    // Исправляем глюк "чёрного экрана" при autoplay на iPhone
    &::-webkit-media-controls {
      display: none !important;
    }

    &::-webkit-media-controls-start-playback-button {
      display: none !important;
      -webkit-appearance: none;
    }
  }

  &__slider-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
  }

  &__slider-list {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    transition: var(--slide-transition);
  }

  &__slider-item {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    opacity: 0.4;
    transition: opacity 0.5s var(--slide-transition), transform 2s var(--slide-transition);

    &.swiper-slide-active {
      opacity: 1;
      transform: scale(1);
      cursor: pointer;
      z-index: 5;
    }

    &.swiper-slide-next,
    &.swiper-slide-prev {
      transform: scale(0.5);
    }
  }

  &__slider-image-wrapper > * {
	  position: absolute;
    inset: 0;
	  perspective: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
	  transform-style: preserve-3d;
    z-index: 4;
  }

  &__slider-image-left {
    perspective-origin: left center;
    clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%); 
    .clients__slider-image {
      animation: image-left 1s ease-in-out infinite;
      animation-direction: alternate;
    }
  }

  &__slider-image-right {
    perspective-origin: right center;
    clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
    margin-left: -.55px;
  // Фикс вертикальной полосы на некоторых дисплеях
    .clients__slider-image {
      animation: image-right 1s ease-in-out infinite;
      animation-direction: alternate;
    }
  }

&__slider-image {
  max-width: 200px;
  width: 100%;
  height: auto;
  position: relative !important; // оставляем absolute для совместимости с обертками
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  z-index: 5;
  aspect-ratio: 3 / 1;
  transform: translateZ(0);
  backface-visibility: hidden;
  transform: translateY(10px);
  will-change: transform;
  animation: fadeInUp 1s ease forwards;
  animation-delay: 0.8s;
  
  @supports not (aspect-ratio: 3 / 1) {
    &::before {
      content: "";
      display: block;
      padding-top: calc(100% / 3); // 3:1 пропорции
    }
  }

  @include laptop {
    max-width: 160px;
  }
    
  @include mobile {
    max-width: 120px;
  }

  &--asml {
    background-image: url('../img/logos/asml.png');
  }

  &--iwc {
    background-image: url('../img/logos/iwc.png');
  }

  &--bb {
    background-image: url('../img/logos/bb.png');
  }

  &--rioleringsdiensten {
    background-image: url('../img/logos/iwc.png')
  }

  &--ministerie {
    background-image: url('../img/logos/bb.png')
  }

  &--strik {
    background-image: url('../img/logos/bb.png')
  }

  &--bouw {
    background-image: url('../img/logos/bb.png')
  }

  &--water {
    background-image: url('../img/logos/bb.png')
  }
}

  &__facts {
    width: 100%;
    margin-bottom: fluid(60, 20);

    // @include laptop {
    //   margin-bottom: rem(40);
    // }

    // @include mobile {
    //   margin-bottom: 0;
    // }
  }

  &__reviews {
    width: 100%;
    display: flex;
    flex-direction: column;
  } 
}



